//Light theme
$color-bg-body-light: #f2f6fe;
$color-bg-api-light: #fefefe;
$color-btn-search-light: #0079fc;
$color-bg-icons-light: #50679c;
$color-bg-container-followers-light: #f5f8ff;


//Dark theme
$color-white: rgb(255, 255, 255);

$color-veryDarkBlue: rgb(20,28,48);
$color-bg-api: rgb(31, 42, 72);

$color-skyBlue: rgb(16,79,158);
$color-verySkyBlue: rgb(0,121,254);


* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

#root {
  	width: 100vw;
	transition: .3s;
	padding: 1.5rem 0 0 0;
	justify-content: center;
	height: 100vh;
  	color: $color-white;
	display: flex;
	background-color: $color-veryDarkBlue;
}

button {
	color: $color-white;
	border: none;
	background: none;
}

svg {
	min-width: 20px;
	min-height: 20px;
	max-width: 20px;
	max-height: 20px;
}

.container-dad {
	max-width: 550px;
	width: 90%;
  	height: 400px;
	gap: 1.4rem;
  	flex-direction: column;
  	display: flex;

	  .container-theme,
	  .search,
	  .container-data-api {
		border-radius: 5px;
	}

	.img-desk {
		display: none;
	}
	.joined-desk {
		display: none;
		color: #ccc;
	}

	.container-theme {
		width: 100%;
		padding-inline: .5rem;
		justify-content: space-between;
    	display: flex;
		align-items: center;

		h2 {
			font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
		}

		.container-btn-theme {
			width: 80px;
			padding: .3rem;
			display: flex;
			cursor: pointer;
			border-radius: 3px;
			align-items: center;

			&:hover {
				transition: .3s;
				background-color: rgba(255, 255, 255, .1);
			}

			.btn-theme {
				width: 100px;
				padding: .5rem 0;
				cursor: pointer;
			}
		}

  	}

	.search {
		width: 100%;
		position: relative;
		padding: .5rem .8rem;
		justify-content: space-between;
    	display: flex;
		border-radius: .5rem;
		background-color: $color-bg-api;
		align-items: center;

		label {
			width: 100%;
			gap: 1rem;
			display: flex;
			align-items: center;

			svg {
				color: $color-verySkyBlue;
			}
		}

		input {
			width: 92%;
			padding: .5rem .2rem;
			outline: none;
			letter-spacing: .5px;
			font-size: 1rem;
			color: $color-white;
			border: none;
			background: none !important;
		
			&::placeholder {
				font-size: .85rem;
			}
		}

		.btn-search {
			width: 80px;
			padding: .9rem .7rem;
			font-family: monospace;
			border-radius: .7rem;
			background-color: $color-verySkyBlue;

			&:active {
				background-color: $color-skyBlue;
			}
			&:hover {
				background-color: rgb(22, 135, 255);
				cursor: pointer;
			}
		}

		[not-found="on"] {
			transition: .5s;
			top: 62px;
			position: absolute;
			left: 54px;
			font-size: .9rem;
			color: rgba(255, 42, 42, 0.8);
		}
		[not-found="off"] {
			transition: .3s;
			display: none;
		}
	}

	.aviso {
		padding: 1.5rem;
		height: auto !important;
		font-family: Verdana, Geneva, Tahoma, sans-serif;
		border-radius: 15px;
	}
	
	.container-data-api {
		justify-content: center;
		height: 100vh;
		font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
		display: flex;
		border-radius: .5rem;
		background-color: $color-bg-api;

		.container-rows {
			width: 100%;
			padding: 1.5rem;
			gap: 1.7rem;
			flex-direction: column;
			display: flex;

			.row1, .row2, .info-user {
				display: flex;
			}

	
			.row1 {
				gap: 1.5rem;
				align-items: center;

				.logo {
					img {
						width: 100px;
						border-radius: 60px;
					}
				}
	
				.info-user {
					gap: .7rem;
					font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
					flex-direction: column;

					h2 {
						font-size: 1.1rem;
					}

					a {
						text-decoration: none;
						font-size: .9rem;
						color: $color-verySkyBlue;
					}
					
					p {
						word-spacing: 3px;
						font-size: .9rem;
						color: #ccc;
					}
				}
			}

			.bio {
				p {
					padding-inline: .2rem;
				}
			}
	
			.row2 {
				padding: 1rem;
				justify-content: space-around;
				border-radius: .5rem;
				background-color: $color-veryDarkBlue;

				.column {
					text-align: center;
					gap: .5rem;
					flex-direction: column;
					display: flex;

					.p-bold {
						font-weight: bold;
					}
				}
			}
	
			.row3 {
				grid-template-columns: 1fr;
				gap: 1rem;
				display: grid;

				.line {
					gap: .5rem;
					display: flex;
					align-items: center;

					p {
						font-size: .9rem;
					}

					a {
						text-decoration: none;
						font-size: .9rem;
						color: $color-verySkyBlue;
					}
				}
			}
		}
	}
	
}

@media (min-width: 560px) {
	.row3 {
		padding: 0 .1rem;
		grid-template-columns: 1fr 1fr !important;
	}
}

@media (min-width: 705px) {

	.img-mob {
		display: none;
	}
	.joined-mob {
		display: none;
	}

	.container-dad {
		max-width: 650px !important;
	}

	.container-data-api {
		padding: 1rem 2rem;

		.row1 {
			justify-content: space-between;

			.joined-desk {
				padding-top: .1rem;
				font-size: .9rem;
				display: block !important;
				align-self: flex-start;
			}
		}

		.row3 {
			grid-template-columns: 35% 65% !important;
		}
	}

	.img-desk {
		margin-top: .7rem;
		display: block !important;

		img {
			width: 105px;
			border-radius: 60px;
		}
	}
}


// Light Theme

#root.light-theme {
	transition: .3s;
	color: $color-veryDarkBlue;
	background-color: $color-bg-body-light;

	.search,
	.container-data-api {
		box-shadow: 0 0 4px rgba(0, 0, 0, .1);
	}

	svg {
		stroke: $color-bg-icons-light;
	}

	svg.twitter {
		fill: $color-bg-icons-light;
	}

	.joined-desk,
	.joined-mob {
		color: $color-bg-icons-light;
	}


	.container-btn-theme {
		.btn-theme {
			color: $color-veryDarkBlue;
		}

		svg {
			stroke: $color-veryDarkBlue;
		}

		&:hover {
			background-color: rgba(0, 0, 0, .1);
		}
	}

	.search,
	.aviso,
	.container-data-api {
		background-color: $color-bg-api-light;
	}

	.search {
		input {
			color: $color-veryDarkBlue;
		
			&::placeholder {
				font-size: .85rem;
				color: $color-veryDarkBlue;
			}
		}
	}
	
	.container-rows {
		.row2 {
			background-color: $color-bg-body-light;
		}
	}
}